@use '../../abstracts/mixins' as *;

.AuthContainer {
    margin: 69px auto 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    width: 356px;

    h3 {
        margin-top: 29px;
    }

    &> :first-child {
        align-self: center;
    }

    &> :last-child {
        margin-top: 22px;
        justify-content: flex-end;
        width: 100%;
        display: flex;
        gap: 12px;
    }
}

.AuthInputContainer {
    margin: 30px 0 40px;
}